import { Component, OnInit } from '@angular/core';
import {Router, Navigation} from '@angular/router';

@Component({
  selector: 'app-AcceptContract',
  templateUrl: './AcceptContract.component.html',
  styleUrls: ['./AcceptContract.component.css']
})
export class AcceptContractComponent implements OnInit {

  constructor(private _router:Router) { }

  ngOnInit() {
  }

  onChangeChechbox(event) {
   if (event.checked==true)
   {
    document.getElementById("btnAcceptContract").removeAttribute('disabled');
   } 
   else
   {
    document.getElementById("btnAcceptContract").setAttribute("disabled", "disabled");
   }
}
btnAcceptContractClick(){
  this._router.navigate(['/layout']);
}
}
